html {
  background-color: #f0f2f5;
}

#loginFrmWrapper {
  margin-top: 15%;
  background-color: white;
  width: 332px;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
  border-radius: 6px;
  box-shadow: 0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%);
}

#loginFrm {
  text-align: center;
}

#loginFrm input {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 300px;
  height: 45px;
  font: 400 13.3333px Arial;
  font-size: 17px;
  border: 1px solid #dddfe2;
  border-radius: 6px;
  margin-bottom: 10px;
  padding: 3px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  padding: 0px 15px 0px 15px;
}

#loginFrm input:focus {
  border: 1px solid #166fe5;
  border-radius: 6px;
}

#loginFrm input:focus-visible, #statusTxt:focus-visible {
  outline: none;
}

#loginFrm button {
  background-color: #1877f2;
  border: none;
  border-radius: 6px;
  font-size: 20px;
  height: 48px;
  width: 332px;
  color: #FFF;
  font-weight: bold;
}

#loginFrm button:hover {
  background-color: #166fe5;
}

.jbLogo {
  text-align: center;
  font-family: arial;
  font-size: 50pt;
  color: #1877f2;
  font-weight: bold;
  margin-top: 20px;
}

#statusUpdaterFrm {
  margin: 30px auto 30px auto;
  width: 40%;
  display: flex;
}

#statusUpdaterFrm button {
  margin-left: 10px;
  border-radius: 9px;
  border: none;
  background-color: rgb(126, 236, 126);
  width: 15%;
  font: 400 13.3333px Arial;
  font-size: 17px;
}

#statusUpdaterFrm button:hover {
  background-color: rgb(63, 216, 63);
}

#statusTxt {
  border-radius: 9px;
  border: 3px solid #dddfe2;
  font: 400 13.3333px Arial;
  font-size: 17px;
  padding: 10px;
  width: 85%;
}

#statusTxt:focus {
  border: 3px solid #166fe5;
}

#statusArea {
  width: 50%;
  margin-left: auto;
  margin-right: auto;
}

.statuses {
  margin: 20px;
  background-color: white;
  border-radius: 10px;  
  padding: 20px;
  border: 1px solid lightgrey;
  overflow: auto;
}

.userName {
  color: grey;
}

.statusTxtArea {
  font-size: 20px;
  margin: 10px 0px 10px 0px;  
}

.statusTime {
  font-size: 0.7em;
  color: grey;
}

@media only screen and (max-width: 600px) {
  #statusUpdaterFrm button {
    width: 20%;
  }
  #statusArea, #statusUpdaterFrm {
    width: 95%;
  }
}